  /* styling all container */
  .container {
    background-color: #eee;
    border-radius: 5%;
    width: 60%;
    margin: 0 auto;
    box-shadow: -1px -1px 6px 8px #888888;
  }
  
  .container h1 {
    text-align: center;
  }
  
  .container .row {
    border-top: 1px solid grey;
    border-bottom: 1px solid grey;
    display: flex;
  }
  
  .col-1-of-2 {
    width: 50%;
  }
  
  .col-1-of-1 {
    width: 100%;
  }
  
  /* select for all */
  select {
    width: 100%;
    background: transparent;
    border: none;
    outline: none;
  }
  
  /* button style for add and delete */
  button {
    display: inline-block;
    border-radius: 10px;
    border: 4px double #cccccc;
    color: #eeeeee;
    text-align: center;
    font-size: 28px;
    padding: 20px;
    width: 200px;
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
    cursor: pointer;
    margin: 5px;
  }
  
  /* style for add button */
  button:nth-of-type(1) {
    background-color: rgb(82, 199, 82);
  }
  
  button:nth-of-type(1):hover {
    background-color: rgb(22, 145, 6);
  }
  
  /* style for delete button */
  button:nth-of-type(2) {
    background-color: #bd4545;
  }
  
  button:nth-of-type(2):hover {
    background-color: red;
  }
  
  [class^="col-"] {
    padding: 2rem;
  }
  
  [class^="col-"]:hover {
    background-color: grey;
  }
  
  /* for all li elements */
  li {
    padding: 20px;
    font-size: 1rem;
    position: relative;
    border-top: 1px solid gray;
    transition: all 1s;
  }
  
  /* for selecting elements whose class attribute is todo__description */
  [class="todo__description"] {
    font-size: 2rem;
    margin-left: 1rem;
  }
  
  /* for selecting elements whose class attribute is todo__category */
  [class^="todo__category"] {
    font-size: 1.5rem;
    text-transform: uppercase;
    padding: 0.6rem;
    color: white;
    position: absolute;
    right: 2rem;
    top: 1.15rem;
  }
  
  /* for selecting elements whose class attribute is todo__description */
  .todo__dueDate {
    display: block;
    margin-left: 2rem;
    margin-top: 1rem;
    font-size: 1.4rem;
  }
  
  /* for coloring different category */
  .todo__category-personal {
    background-color: blueviolet;
  }
  
  .todo__category-work {
    background-color: hsla(240, 100%, 50%, 0.534);
  }
  
  .todo__category-school {
    background-color: red;
  }
  
  .todo__category-cleaning {
    background-color: green;
  }

  